@tailwind base;
@tailwind components;
@tailwind utilities;

/* Customizações globais */
body {
  @apply bg-gray-100 text-gray-800 antialiased;
  font-family: 'Inter', sans-serif;
  margin: 0;
  padding: 0;
}

/* Ajuste global para cabeçalhos */
h1,
h2,
h3,
h4,
h5,
h6 {
  @apply font-bold text-gray-800;
}

/* Links padrões */
a {
  @apply text-blue-500 hover:text-blue-700 transition-colors;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

/* Estilização para botões globais */
button {
  @apply bg-blue-500 text-white font-semibold py-2 px-4 rounded hover:bg-blue-600 transition-colors;
}

button:disabled {
  @apply bg-gray-400 cursor-not-allowed;
}

/* Estilo global para inputs */
input,
select {
  @apply border border-gray-300 rounded-md py-2 px-4 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent;
}